import React from 'react';
import { Link, NavLink }    from 'react-router-dom';
import { Menu, Icon } from 'antd';
import './index.css';

const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;


class NavSide extends React.Component{
	constructor(props){
		super(props);
	}
	handleClick =(e)=>{
		console.log(e)
	}
	render(){
		return(
			<Menu
			className="menuList"
			onClick={this.handleClick}
			style={{ width: 260,height:'100%' }}
			defaultSelectedKeys={['1']}
			defaultOpenKeys={['sub1']}
			mode="inline">
				<Menu.Item key="1"><NavLink to="/"><span><Icon type="home" /></span></NavLink></Menu.Item>
				<SubMenu key="sub1" title={<span><Icon type="appstore" /><span></span></span>}>
			        <Menu.Item key="2"></Menu.Item>
			        <Menu.Item key="3"></Menu.Item>
		        </SubMenu>
		        <SubMenu key="sub2" title={<span><Icon type="setting" /><span></span></span>}>
		          	<Menu.Item key="4"></Menu.Item>
		        </SubMenu>
		        <SubMenu key="sub3" title={<span><Icon type="setting" /><span></span></span>}>
		          	<Menu.Item key="5"></Menu.Item>
		        </SubMenu>
			</Menu>
		)
	}
}
export default NavSide;


