import React from 'react';

import NavSide from '../nav-slide/index';


class Layout extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        return (
            <div id="wrapper">
                <NavSide />
                {this.props.children}
            </div>
        );
    }
}

export default Layout;



